/* Section Slogan */

.section-slogan {
	border-top: 1px solid #e8e8e8;
	border-bottom: 1px solid #e8e8e8;
	display: flex;
	justify-content: center;

	&.container {
		@media (max-width: 930px) {
			width: auto;
		}
	}
}

.section-slogan__content {
	padding: 65px 100px;
	width: 950px;
	background: $bg-block;

	@media (max-width: 930px) {
		padding: 61px 50px;
		width: auto;
	}

	@media (max-width: 599px) {
		padding: 61px 32px;
	}
}

.section-slogan__text {
	margin-top: 13px;
	margin-left: 97px;
	display: inline-block;

	font-weight: 600;
	font-size: 28px;
	line-height: 1.57;
	color: $black;
	text-transform: lowercase;

	@media (max-width: 930px) {
		margin-top: 28px;
		margin-left: 0;
	}
}
