/* Section Outsource */

.section-outsource {
	position: relative;

	&::before {
		content: "";
		display: block;
		width: 838px;
		height: 1265px;

		position: absolute;
		z-index: -1;
		bottom: -72px;
		right: 0;

		background-image: url("./../img/bg/bg-outsource.png");
		background-repeat: no-repeat;
		background-position: right;
		opacity: 0.6;

		@media (max-width: 930px) {
			background-image: url("./../img/bg/bg-outsource-m.png");
			bottom: 0;
			top: -15%;
			height: 320px;
			width: 210px;
		}
	}

	& .container {
		&::after {
			padding-bottom: 100px;
			margin-right: -307px;
			content: "";
			display: block;
			border-bottom: 1px solid $gray-elements;

			@media (max-width: 1199px) {
				padding-bottom: 70px;
			}
		}
	}
}

.section-outsource__content {
	display: grid;
	grid-auto-flow: column;
	grid-template-columns: repeat(2, auto);
	gap: 30px;
	align-items: flex-start;

	@media (max-width: 930px) {
		grid-template-columns: repeat(1, auto);
		grid-template-rows: repeat(2, auto);
		gap: 50px;
	}
}

.section-outsource__desc {
	display: grid;
	grid-template-rows: repeat(3, auto);
	gap: 47px;

	@media (max-width: 930px) {
		gap: 30px;
	}
}

.section-outsource__info {
	width: 362px;

	@media (max-width: 1199px) {
		width: 291px;
		padding: 70px 50px;
	}

	@media (max-width: 930px) {
		width: auto;
		grid-auto-flow: column;
		grid-template-columns: repeat(2, auto);
		grid-template-rows: 1fr;
	}

	@media (max-width: 599px) {
		grid-auto-flow: row;
		grid-template-columns: auto;
		grid-template-rows: repeat(2, auto);
	}
}

.section-outsource__info-title {
	font-size: 90px;
}
