/* Form feedback */

.form-feedback__wrap {
	padding: 55px;
	grid-row: 1/-1;

	background-color: $orange;

	display: grid;
	justify-items: center;
	align-items: center;

	@media (max-width: 1199px) {
		padding: 40px;
	}

	@media (max-width: 930px) {
		grid-row: 6;
		grid-column: 1/-1;

		margin-left: -12px;
		margin-right: -12px;
	}
}

.form-feedback {
	width: 264px;

	@media (max-width: 1199px) {
		width: auto;
	}
}

.form-feedback__title {
	margin-bottom: 40px;
	font-weight: 600;
	font-size: 28px;
	line-height: 1.57;
	text-align: center;
	color: $white;
}

.form-feedback__input {
	margin-bottom: 10px;
	padding: 20px;
	display: block;
	width: 100%;

	font-weight: 400;
	font-size: 14px;
	line-height: 1.29;
	outline: 0;
	border: 1px solid $white;
	transition: 0.3s linear;

	&::placeholder {
		color: $gray;
	}

	&:hover {
		box-shadow: 1px 1px 5px $white;
	}

	&:focus {
		border: 1px solid #d89a44;
		box-shadow: 1px 1px 3px #794803;
	}
}

.form-feedback__textarea {
	margin-bottom: 16px;
	padding: 20px;

	display: block;
	width: 100%;
	height: 120px;

	resize: none;

	font-weight: 400;
	font-size: 14px;
	line-height: 1.29;
	border: 1px solid $white;

	outline: 0;
	transition: 0.3s linear;

	&::placeholder {
		color: $gray;
	}

	&:hover {
		box-shadow: 1px 1px 5px $white;
	}

	&:focus {
		border: 1px solid #d89a44;
		box-shadow: 1px 1px 3px #794803;
	}
}

.form-feedback__agreement {
	margin-bottom: 23px;

	font-family: "Montserrat", sans-serif;
	font-weight: 400;
	font-size: 14px;
	line-height: 1.29;
	color: $white;
}

.form-feedback__button {
	margin: 0 auto;
}

/*  */

.alarm {
	position: relative;
	padding: 0.75rem 1.25rem;
	margin-bottom: 1rem;
	border: 1px solid transparent;
	border-radius: 0.25rem;
}

.alert-success {
	color: #721c24;
	background-color: #f8d7da;
	border-color: #f5c6cb;
}

.alert-success {
	color: #155724;
	background-color: #d4edda;
	border-color: #c3e6cb;
}
