/* Header */

.header {
	position: relative;
	padding-top: 20px;
	padding-bottom: 43px;
	z-index: 99;

	&::after {
		content: "";
		display: block;
		width: 900px;
		height: 100%;

		position: absolute;
		z-index: -99;
		top: 0;
		left: 0;

		background-image: url("./../img/bg/bg-header.png");
		background-position: left;
		background-repeat: no-repeat;
		opacity: 0.6;

		@media (max-width: 930px) {
			background-image: url("./../img/bg/bg-header-m.png");
			height: 328px;
			width: 320px;
		}
	}

	@media (max-width: 1199px) {
		padding-top: 10px;
		padding-bottom: 39px;
	}
}

.header__top {
	padding-bottom: 18px;
	border-bottom: 2px solid $main-blue;

	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header__logo {
	width: 162px;
	height: 61px;

	@media (max-width: 930px) {
		width: 120px;
		height: 45px;
	}
}

.header__contacts {
	display: grid;
	grid-auto-flow: column;
	align-items: center;
	column-gap: 32px;

	@media (max-width: 930px) {
		display: none;
	}
}

.header__buttons-wrap {
	display: grid;
	grid-auto-flow: column;
	gap: 11px;

	@media (max-width: 930px) {
		display: none;
	}
}

.header__link {
	position: relative;

	font-weight: 500;
	font-size: 16px;
	line-height: 28px;
	text-decoration: none;

	outline: 0;
	transition: 0.3s linear;

	&::after {
		content: "";
		display: block;
		width: 20px;
		height: 20px;

		position: absolute;
		top: 15%;
		left: -28px;

		background-image: url("./../img/header/phone.svg");
		background-repeat: no-repeat;
	}

	&:hover,
	&:focus {
		text-shadow: 1px 1px 10px $main-blue;
	}
}

.header__social {
	display: grid;
	grid-template-columns: 60px 60px;
	gap: 11px;

	@media (max-width: 930px) {
		display: none;
	}
}

.header__info {
	padding-top: 73px;
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: space-between;
	align-items: center;
	gap: 230px;

	@media (max-width: 1199px) {
		gap: 50px;
	}

	@media (max-width: 930px) {
		padding-top: 40px;
		gap: 25px;
	}

	@media (max-width: 599px) {
		grid-template-columns: 1fr;
		grid-template-rows: 225px 240px;
		justify-items: center;
		text-align: left;
	}
}

.header__img {
	width: 460px;
	height: 476px;

	@media (max-width: 1199px) {
		width: auto;
		height: auto;
	}

	@media (max-width: 599px) {
		width: 232px;
		height: 225px;
	}
}

.header__title {
	margin-bottom: 71px;

	font-weight: 600;
	font-size: 56px;
	line-height: 2.5;

	@media (max-width: 1199px) {
		margin-bottom: 40px;

		font-size: 50px;
		line-height: 2;
	}

	@media (max-width: 930px) {
		margin-bottom: 20px;

		line-height: 1.7;
		font-size: 36px;
	}

	@media (max-width: 599px) {
		font-size: 28px;
		line-height: 1.57;
	}
}

.header__list {
	margin-left: 25px;
}
