/* Section Team */

.section-team {
	&::after {
		margin-right: -307px;
		content: "";
		display: block;
		border-bottom: 1px solid $gray-elements;
	}
}

.section-team__top {
	display: flex;
	justify-content: space-between;
	margin-bottom: 32px;

	@media (max-width: 930px) {
		display: block;
	}
}

.section-team__wrap {
	width: 1146px;
	margin-right: auto;
	margin-left: auto;
	padding-top: 100px;
	padding-bottom: 102px;

	@media (max-width: 1199px) {
		padding-top: 70px;
		padding-bottom: 70px;
		width: 876px;
	}

	@media (max-width: 930px) {
		width: 100%;
	}
}

.section-team__subtitle {
	margin-bottom: 45px;
}
