/* Modal Window */

.modal-block {
	z-index: 97;
	display: none;
	padding: 70px 15px;
	text-align: center;
	position: fixed;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #fff;

	opacity: 0;
	transition: 0.3s linear;

	.form-feedback__wrap {
		position: relative;
	}
}

.modal-block--active {
	display: grid;
	justify-content: center;
	justify-items: center;
	align-content: center;
	gap: 30px;
	z-index: 100;
	top: 0;
	opacity: 1;
	transition: 0.3s linear;
}

.btn-close {
	position: absolute;
	display: block;
	width: 40px;
	height: 40px;
	top: 10px;
	right: 10px;

	background-color: inherit;
	border: 1px solid transparent;
	border-radius: 50%;

	cursor: pointer;
	outline: 0;
	transition: 0.3s linear;

	fill: $gray;

	&:hover,
	&:focus {
		border: 1px solid $main-blue;
		fill: $main-blue;
	}
}

.btn-close__icon {
	width: 24px;
	height: 24px;
}
