/* Section Practice */

.section-practice {
	padding-top: 112px;
	padding-bottom: 100px;

	position: relative;

	&::after {
		content: "";
		display: block;
		width: 838px;
		height: 1310px;

		position: absolute;
		z-index: -1;
		top: 0;
		left: -33px;

		background-image: url("./../img/bg/bg-practice.png");
		background-repeat: no-repeat;
		background-position: left;
		opacity: 0.6;

		@media (max-width: 930px) {
			background-image: none;
		}
	}

	& .container {
		@media (max-width: 930px) {
			width: 100%;
		}
	}

	@media (max-width: 1199px) {
		padding-top: 70px;
		padding-bottom: 80px;
	}
}

.section-practice__desc {
	margin-bottom: 34px;
	display: grid;
	grid-template-columns: repeat(2, auto);
	grid-template-rows: repeat(2, auto);
	gap: 30px;
	align-items: flex-start;

	@media (max-width: 930px) {
		grid-template-columns: repeat(1, auto);
		grid-template-rows: repeat(3, auto);
	}
}

.section-practice__wrap {
	p {
		font-weight: 400;
		font-size: 20px;
		line-height: 1.6;
	}

	& p + p {
		margin-top: 15px;
	}
}

.section-practice__title {
	margin-bottom: 34px;
}

.section-practice__subtitle {
	margin-bottom: 49px;
}

.section-practice__block {
	padding: 43px 64px;
	background: $bg-block;
	border: 1px solid $gray-elements;

	@media (max-width: 930px) {
		order: 4;
		padding: 63px 30px;
	}
}

.section-practice__block-title {
	margin-bottom: 27px;
	font-weight: 800;
	font-size: 16px;
	line-height: 1.88;
	text-transform: uppercase;
	color: $orange;
}

.section-practice__block-list {
	margin-left: 17px;
	& > * + * {
		margin-top: 32px;
	}
}

.section-practice__text {
	grid-column: 1/3;
	width: 880px;

	font-weight: 600;
	font-size: 28px;
	line-height: 1.57;
	color: $black;

	@media (max-width: 930px) {
		grid-column: 1/2;
		width: auto;
	}
}

.section-practice__img {
	position: relative;
	margin-left: -250px;
	height: 560px;
	width: 1354px;
	background-image: url("../../img/practice/practice.jpg");

	@media (max-width: 1199px) {
		margin-left: 0;
		width: 880px;
		height: 363px;
	}

	@media (max-width: 930px) {
		width: auto;
		height: 670px;

		background-image: url("../../img/practice/practice-mobile.jpg");
		background-repeat: no-repeat;
		background-size: cover;
	}
}

.section-info {
	padding: 56px 67px;
	padding-bottom: 74px;
	width: 556px;

	display: grid;
	align-items: center;
	grid-template-rows: repeat(2, auto);
	gap: 24px;

	background: $orange;
	color: $white;

	@media (max-width: 930px) {
		gap: 22px;
	}
}

.section-practice__info {
	position: absolute;
	top: 100px;
	right: -100px;

	@media (max-width: 1299px) {
		right: 0;
	}

	@media (max-width: 1199px) {
		width: 400px;
		padding: 64px 30px;
		top: 35px;
	}

	@media (max-width: 930px) {
		top: auto;
		bottom: 0;
		width: auto;
		height: auto;
	}
}

.section-info__title {
	font-weight: 900;
	font-size: 60px;
	line-height: 1;

	@media (max-width: 1199px) {
		font-size: 40px;
	}
}

.section-info__text {
	font-weight: 600;
	font-size: 16px;
	line-height: 2;
}
