/* Button */

.button {
	display: block;
	padding-left: 36px;
	padding-right: 36px;
	background: $main-blue;
	border: $main-blue;

	font-weight: 600;
	font-size: 14px;
	text-transform: uppercase;
	color: $white;
	line-height: 60px;

	outline: 0;
	cursor: pointer;
	transition: 0.3s linear;

	&:hover {
		box-shadow: 1px 1px 5px $main-blue;
		background: $light-blue;
	}

	&:focus {
		box-shadow: 1px 1px 7px $dark-blue;
		background: $dark-blue;
	}
}

.button-social {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 60px;
	height: 60px;
	border: 1px solid $gray-elements;
}
