/* Section Who */

.section-who {
	padding-top: 75px;
	margin: 0 auto;

	@media (max-width: 1199px) {
		padding-top: 70px;
	}
}

.section-who__content {
	position: relative;
	margin-bottom: 97px;
}

.section-who__head {
	position: absolute;
	top: -10px;

	@media (max-width: 930px) {
		position: relative;
		margin-bottom: 40px;
		top: 0;
	}
}

.section-who__title {
	margin-bottom: 21px;
}

.section-who__row {
	display: grid;
	grid-template-columns: repeat(2, auto);
	grid-template-rows: repeat(3, auto);
	grid-auto-flow: column;
	gap: 50px 30px;
	grid-template-areas:
		". who-block3"
		"who-block1 who-block4"
		"who-block2 who-block5";

	@media (max-width: 930px) {
		display: block;
		margin-right: 30px;
		width: auto;
	}
}

.section-who__img {
	background-image: url("../../img/who/who-img.jpg");
	height: 350px;
	margin-left: -305px;

	@media (max-width: 1199px) {
		margin-left: 0;
		background-position: center;
	}

	@media (max-width: 930px) {
		margin-left: -150px;
		margin-right: -150px;
		width: auto;
	}

	@media (max-width: 599px) {
		margin-left: -70px;
		margin-right: 0;
		background-image: url("../../img/who/who-img-mobile.jpg");
		background-position: inherit;
		background-repeat: no-repeat;
		background-size: 660px;
	}

	@media (max-width: 450px) {
		background-size: 500px;
	}

	@media (max-width: 370px) {
		background-size: 420px;
	}
}

/* Who block */

.who-block {
	padding-left: 41px;
	border-left: 2px solid $main-blue;
	outline: 0;
	transition: 0.3s linear;

	@media (max-width: 930px) {
		max-width: 240px;
		padding-right: 15px;
		padding-left: 15px;

		&:focus {
			border-left: 2px solid $orange;
		}
	}
}

.who-block__title {
	font-weight: 800;
	font-size: 16px;
	line-height: 1.88;
	text-transform: uppercase;
}

.who-block__number {
	color: $orange;
}

.who-block1 {
	grid-area: who-block1;
}

.who-block2 {
	grid-area: who-block2;
}

.who-block3 {
	grid-area: who-block3;
}

.who-block4 {
	grid-area: who-block4;
}

.who-block5 {
	grid-area: who-block5;
}
