/* Section About */

.section-about {
	position: relative;

	&::after {
		content: "";
		display: block;
		width: 838px;
		height: 1265px;

		position: absolute;
		z-index: -1;
		top: 0;
		right: 0;

		background-image: url("./../img/bg/bg-about.png");
		background-repeat: no-repeat;
		background-position: right;
		opacity: 0.6;

		@media (max-width: 930px) {
			background-image: url("./../img/bg/bg-about-m.png");
			height: 320px;
			width: 210px;
		}
	}

	& .container {
		&::after {
			margin-right: -307px;
			content: "";
			display: block;
			border-bottom: 1px solid $gray-elements;

			@media (max-width: 930px) {
				display: none;
			}
		}
	}
}

.section-about__wrap {
	margin-left: 85px;
	padding-top: 100px;
	width: 950px;
	border-left: 1px solid $gray-elements;

	@media (max-width: 1199px) {
		padding-top: 50px;
		margin-left: 0;
		width: 876px;
	}

	@media (max-width: 930px) {
		border-left: none;
		width: auto;
	}

	@media (max-width: 599px) {
		width: auto;
		text-align: left;
	}
}

.section-about__content {
	padding-left: 100px;
	padding-right: 100px;

	& > p + p {
		margin-top: 35px;
	}

	@media (max-width: 1199px) {
		padding-left: 30px;
		padding-right: 30px;
	}

	@media (max-width: 930px) {
		padding-left: 15px;
		padding-right: 15px;
	}
}

.section-about__title {
	margin-bottom: 35px;

	@media (max-width: 1199px) {
		margin-bottom: 30px;
	}
}

.section-about__subtitle {
	margin-bottom: 35px;
}

.section-about__text {
	font-weight: 500;
	font-size: 16px;
	line-height: 1.75;
}

.section-about__block {
	margin-top: 70px;
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: #f6f8f9;

	& p {
		font-weight: 600;
		font-size: 16px;
		line-height: 2;
	}

	@media (max-width: 1199px) {
		padding-left: 30px;
		padding-right: 30px;
		margin-top: 53px;
	}

	@media (max-width: 930px) {
		padding-left: 15px;
		padding-right: 15px;
	}
}
