/* Slider-arrow */

.slider-arrows {
	display: flex;

	@media (max-width: 930px) {
		display: none;
	}
}

.slider-arrow {
	width: 60px;
	height: 60px;
	border: 1px solid $gray-elements;
	background: #f6f8f9;

	display: grid;
	align-items: center;
	justify-items: center;
	fill: $gray;
	outline: 0;

	transition: 0.3s linear;

	&:hover {
		border: 1px solid $main-blue;
		box-shadow: 1px 1px 5px $main-blue;
		background: $light-blue;
		fill: $white;
	}

	&:focus {
		border: 1px solid $dark-blue;
		box-shadow: 1px 1px 7px $dark-blue;
		background: $dark-blue;
		fill: $white;
	}
}

.slider-arrow__left,
.slider-arrow__right {
	width: 20px;
	height: 20px;
}
