/* Reset and base styles  */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
	overflow-x: hidden;
}

body {
	margin: 0;
	font-family: "Montserrat", sans-serif;
	color: $black;
	overflow-x: hidden;
}

input,
textarea,
button {
	font-family: inherit;
	padding: 0;
}

label {
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

ul {
	margin: 0;
	padding: 0;
	list-style-type: circle;
	position: relative;
}

li::marker {
	color: $orange;
}

fieldset {
	border: none;
	margin: 0;
	padding: 0;
}

legend {
	display: block;
	margin: 0;
	padding: 0;
	border: none;
}

/* Grid styles */

.container {
	z-index: 8;
	position: relative;

	margin: 0 auto;
	padding-left: 12px;
	padding-right: 12px;

	width: 1146px;

	@media (max-width: 1199px) {
		width: 910px;
	}

	@media (max-width: 930px) {
		width: 100%;
	}
}

/* Utility */

.none {
	display: none !important;
}

/* Sticky Footer */

html {
	height: 100%;
}

body {
	height: 100%;
	display: flex;
	flex-direction: column;
}

body > * {
	flex-shrink: 0;
}

.footer {
	margin-top: auto;
}

/* General */

.section__title {
	font-weight: 600;
	font-size: 40px;
	line-height: 1.35;

	@media (max-width: 1199px) {
		line-height: 2;
	}

	@media (max-width: 930px) {
		font-size: 36px;
		line-height: 1.57;
	}

	@media (max-width: 599px) {
		font-size: 28px;
	}
}

.section__subtitle {
	font-weight: 800;
	font-size: 16px;
	line-height: 1.87;
	text-transform: uppercase;
	color: $main-blue;
}

.section__list {
	font-weight: 500;
	font-size: 16px;
	line-height: 2.5;

	@media (max-width: 930px) {
		font-size: 14px;
	}

	@media (max-width: 599px) {
		font-size: 16px;
		line-height: 1.88;
	}
}
