/* Section Services */

.section-services {
	padding-bottom: 100px;

	@media (max-width: 1199px) {
		padding-bottom: 70px;
	}

	&.container {
		@media (max-width: 930px) {
			width: 590px;
		}

		@media (max-width: 620px) {
			width: auto;
		}
	}
}

.section-services__title {
	margin-bottom: 46px;
}

.section-services__grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	gap: 30px;

	@media (max-width: 1199px) {
		gap: 20px;
	}

	@media (max-width: 930px) {
		grid-template-columns: 1fr;
		grid-template-rows: repeat(4, auto);
	}
}

.service-block {
	padding-top: 58px;
	padding-left: 100px;
	padding-bottom: 40px;

	background: $bg-block;
	border: 1px solid $gray-elements;

	@media (max-width: 1199px) {
		padding-top: 111px;
		padding-left: 70px;
		padding-right: 30px;
	}

	@media (max-width: 620px) {
		padding-left: 50px;
		padding-right: 30px;
	}
}

.service-block__header {
	position: relative;
}

.service-block__title {
	margin-bottom: 12px;
	font-weight: 800;
	font-size: 16px;
	line-height: 1.88;
	text-transform: uppercase;

	@media (max-width: 620px) {
		margin-bottom: 9px;
	}
}

.service-block__icon {
	position: absolute;
	left: -54px;
	top: -10px;
	width: 40px;
	height: 40px;
	fill: $main-blue;

	@media (max-width: 1199px) {
		left: 0;
		top: -60px;
	}
}

.service-block__list {
	margin-left: 15px;
}
