/* Social Link */

.social-link {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 60px;
	height: 60px;
	border: 1px solid $gray-elements;

	cursor: pointer;
	outline: 0;
	transition: 0.3s linear;

	&:hover {
		box-shadow: 1px 1px 3px $main-blue;
	}

	&:focus {
		border: 1px solid $main-blue;
		box-shadow: 1px 1px 3px $main-blue;
	}
}

.social-link__icon {
	height: 24px;
	width: 24px;

	&--viber {
		fill: #7b519d;
	}

	&--telegram {
		fill: #40b0ef;
	}

	&--facebook {
		width: 13px;
		fill: #3b5998;
	}
}
