/* Section Start */

.section-start {
	padding-top: 100px;
	padding-bottom: 65px;

	@media (max-width: 1199px) {
		padding-top: 43px;
		padding-bottom: 70px;
	}
}

.section-start__header {
	margin-bottom: 49px;
}

.section-start__row {
	display: grid;
	grid-template-columns: repeat(3, auto);
	gap: 30px;

	@media (max-width: 930px) {
		gap: 50px;
		grid-template-columns: repeat(1, auto);
		grid-template-rows: repeat(3, auto);
	}
}

.section-start__card {
	display: grid;

	@media (max-width: 930px) {
		grid-template-columns: 50px auto;
		grid-template-rows: repeat(2, auto);
		gap: 20px;
		align-items: center;
	}

	p {
		@media (max-width: 930px) {
			grid-column: 1/-1;
		}
	}
}

.section-start__title {
	margin-bottom: 11px;

	font-weight: 800;
	font-size: 16px;
	line-height: 1.88;
	text-transform: uppercase;
	color: $orange;

	@media (max-width: 930px) {
		margin-bottom: 0;
	}
}

.section-start__number {
	margin-bottom: 23px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	background-color: $main-blue;

	color: $white;
	font-weight: 600;
	font-size: 28px;
	line-height: 1.57;

	@media (max-width: 930px) {
		margin-bottom: 0;
	}
}
