/* Section Clients */

.section-clients {
	padding-top: 98px;
	position: relative;

	&::after {
		content: "";
		display: block;
		width: 838px;
		height: 1265px;

		position: absolute;
		z-index: -1;
		top: -180px;
		right: -184px;

		background-image: url("./../img/bg/bg-clients.png");
		background-repeat: no-repeat;
		background-position: right;
		opacity: 0.6;

		@media (max-width: 930px) {
			height: 320px;
			width: 211px;

			top: -15px;
			right: 0;
			background-image: url("./../img/bg/bg-clients-m.png");
		}
	}

	& .container {
		&::after {
			padding-bottom: 100px;
			margin-right: -307px;
			content: "";
			display: block;
			border-bottom: 1px solid $gray-elements;

			@media (max-width: 1199px) {
				padding-bottom: 70px;
			}
		}
	}

	@media (max-width: 1199px) {
		padding-top: 70px;
	}
}

.section-clients__top {
	margin-bottom: 50px;
	display: flex;
	justify-content: space-between;
}

.section-clients__row {
	display: grid;
	grid-template-columns: repeat(6, 166px);
	gap: 30px;

	@media (max-width: 1199px) {
		gap: 22px;
	}

	@media (max-width: 930px) {
		grid-template-columns: repeat(6, 186px);
	}
}

/* Clients card */

.client-card {
	border: 1px solid $gray-elements;

	@media (max-width: 930px) {
		width: 186px;
		min-height: 90px;
	}
}

.client-card__img {
	display: flex;
	justify-content: center;
	align-items: center;

	@media (max-width: 930px) {
		min-width: 100%;
		object-fit: cover;
	}
}
