/* Section Articles */

.section-articles {
	padding-top: 95px;

	&::after {
		padding-bottom: 100px;
		margin-right: -307px;
		content: "";
		display: block;
		border-bottom: 1px solid $gray-elements;
	}

	@media (max-width: 1199px) {
		padding-top: 60px;

		&::after {
			padding-bottom: 64px;
		}
	}
}

.section-articles__top {
	margin-bottom: 50px;
	display: flex;
	justify-content: space-between;
}

.section-articles__row {
	display: grid;
	grid-template-columns: repeat(3, 362px);
	gap: 30px;
	align-items: flex-start;

	@media (max-width: 1199px) {
		grid-template-columns: repeat(3, 240px);
		justify-content: space-between;
	}

	@media (max-width: 930px) {
		gap: 20px;
	}
}

/* Article card */

.article-card {
	display: grid;
	grid-template-rows: 230px 1fr;
	gap: 14px;

	@media (max-width: 599px) {
		max-width: 320px;
	}
}

.article-card__img {
	position: relative;

	img {
		height: 230px;
		object-fit: cover;
	}
}

.article-card__date {
	padding: 6px 17px;
	background-color: $main-blue;

	font-weight: 400;
	font-size: 14px;
	line-height: 1.29;
	color: $white;

	position: absolute;
	bottom: 10px;
	left: 10px;
}

.article-card__text {
	font-weight: 400;
	font-size: 18px;
	line-height: 1.56;
}
