/* Section Contacts */

.section-contacts {
	padding-top: 100px;
	padding-bottom: 81px;

	@media (max-width: 1199px) {
		padding-top: 60px;
		padding-bottom: 60px;
	}

	@media (max-width: 930px) {
		padding-bottom: 0;
	}
}

.section-contacts__title {
	margin-bottom: 39px;
}

.section-contacts__subtitle {
	margin-bottom: 41px;
	width: 558px;

	@media (max-width: 930px) {
		width: auto;
	}
}

.section-contacts__content {
	display: grid;
	grid-template-columns: 560px 460px;
	grid-template-rows: repeat(4, auto);
	grid-auto-flow: column;
	justify-content: space-between;
	align-items: flex-start;
	gap: 31px;

	@media (max-width: 1199px) {
		grid-template-columns: repeat(2, auto);
		grid-template-rows: repeat(4, auto);
		gap: 20px;
		justify-content: flex-start;
	}

	@media (max-width: 930px) {
		grid-template-columns: auto auto;
		grid-template-rows: repeat(4, auto);
	}

	@media (max-width: 599px) {
		grid-template-columns: auto;
		grid-template-rows: repeat(5, auto);
		justify-self: center;
		gap: 50px;
	}
}

.section-contacts__contact-list {
	display: grid;
	row-gap: 24px;

	font-weight: 400;
	font-size: 14px;
	line-height: 2.14;
}

.section-contacts__link {
	font-weight: 800;
	color: $black;
	text-decoration: none;
	outline: 0;
	transition: 0.3s linear;

	&:hover,
	&:focus {
		text-shadow: 1px 1px 10px $main-blue;
	}
}

.section-contacts__tel {
	display: grid;
	grid-template-columns: 100px 150px;
	grid-template-rows: repeat(2, 1fr);
	grid-auto-flow: column;
	gap: 2px;
	height: 60px;
}

.section-contacts__address {
	display: grid;
	grid-template-columns: 100px auto;
	grid-template-rows: repeat(2, auto);
	grid-auto-flow: column;
	gap: 2px;

	a {
		color: $black;
		text-decoration: none;
		outline: 0;
		transition: 0.3s linear;

		&:hover,
		&:focus {
			text-shadow: 1px 1px 8px $main-blue;
		}
	}
}

.section-contacts__social {
	display: grid;
	grid-template-columns: repeat(4, 60px);
	gap: 10px;

	@media (max-width: 930px) {
		grid-column: 2;
		grid-template-columns: repeat(2, 60px);
		grid-template-rows: repeat(2, 60px);
		align-self: center;
		justify-self: center;
	}

	@media (max-width: 599px) {
		grid-column: 1;
		grid-row: 2;
		grid-template-columns: repeat(4, 60px);
		grid-template-rows: repeat(1, 60px);
		justify-self: left;
	}
}

.section-contacts__map {
	width: 557px;
	height: 190px;

	@media (max-width: 1199px) {
		width: 460px;
	}

	@media (max-width: 930px) {
		width: 100%;
		grid-column: 1/-1;
		grid-row: 3;
	}
}

/* Subscribe */

.subs {
	> * + * {
		margin-top: 25px;
	}
	font-weight: 500;
	font-size: 16px;
	line-height: 1.75;

	@media (max-width: 930px) {
		grid-column: 1/-1;
		grid-row: 4;

		> * + * {
			margin-top: 45px;
		}
	}
}

.subs__form {
	position: relative;

	@media (max-width: 930px) {
		max-width: 490px;
	}
}

.subs__input {
	padding: 20px;
	padding-right: 60px;

	width: 100%;
	background: $bg-block;
	border: 1px solid $gray-elements;

	font-weight: 400;
	font-size: 14px;
	line-height: 1.29;

	outline: 0;
	cursor: pointer;
	transition: 0.3s linear;

	&::placeholder {
		color: $gray;
	}

	&:hover {
		box-shadow: 1px 1px 3px $main-blue;
	}

	&:focus {
		border: 1px solid $main-blue;
		box-shadow: 1px 1px 3px $main-blue;
	}
}

.subs__button {
	position: absolute;
	width: 60px;
	height: 60px;
	display: block;

	right: 0;
	top: 0;

	fill: $main-blue;
	background-color: inherit;
	outline: 0;
	border: 0;
}

.subs__icon {
	width: 24px;
	height: 24px;
}

.subs__input,
.subs__button {
	transition: 0.3s linear;

	&:hover {
		fill: $dark-blue;
	}

	&:focus {
		fill: $orange;
	}
}
