/* Slider */

.slider {
	display: grid;
	grid-template-columns: repeat(4, 264px);
	grid-column-gap: 30px;

	@media (max-width: 930px) {
		grid-template-columns: repeat(4, 240px);
		grid-column-gap: 20px;
	}
}

/* slider-item */

.slider-item {
	display: grid;
	justify-items: center;
	text-align: center;
	width: 264px;

	@media (max-width: 930px) {
		width: 240px;
	}
}

.slider-item__img {
	margin-bottom: 18px;
}

.slider-item__title {
	margin-bottom: 10px;
	min-height: 56px;

	font-weight: 400;
	font-size: 18px;
	line-height: 1.56;
}

.slider-item__specials {
	font-weight: normal;
	font-size: 14px;
	line-height: 1.29;
	color: $gray;
}
