/* Section Form */

.section-form {
	padding: 36px 98px;
	background-color: $orange;
	display: flex;
	justify-content: space-between;
	align-items: center;

	@media (max-width: 1199px) {
		width: 888px;
		padding: 36px 50px;
	}

	@media (max-width: 930px) {
		flex-direction: column;
		justify-content: center;
		padding: 36px 15px;
		width: auto;
	}

	@media (max-width: 599px) {
		padding: 42px 7px;
	}
}

.section-form__content {
	font-weight: 800;
	font-size: 16px;
	line-height: 1.87;
	text-transform: uppercase;

	color: $white;

	@media (max-width: 1199px) {
		font-size: 14px;
	}

	@media (max-width: 930px) {
		font-size: 16px;
		text-align: center;
		margin-bottom: 20px;
	}
}
