/* Section Call to Action */

.section-cta {
	background-color: $orange;
	padding: 47px 98px;
	color: $white;

	@media (max-width: 930px) {
		padding: 42px 46px;
	}

	@media (max-width: 340px) {
		padding: 60px 30px;
	}
}

.section-cta__content {
	display: grid;
	grid-template-columns: 1fr 222px;
	gap: 76px;
	align-items: center;

	@media (max-width: 930px) {
		grid-template-columns: auto;
		justify-items: center;
		gap: 30px;
	}

	@media (max-width: 599px) {
		gap: 53px;
	}
}

.section-cta__title {
	margin-bottom: 17px;
	font-weight: 600;
	font-size: 28px;
	line-height: 1.57;
}

.section-cta__text {
	font-weight: 500;
	font-size: 16px;
	line-height: 1.75;
}

.section-cta__button {
	outline: 0;
}
