/* Mobile menu */

.mobile-menu {
	display: none;
	padding: 70px 15px;
	text-align: center;

	@media (max-width: 930px) {
		position: fixed;
		top: -100%;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #fff;
		opacity: 0;
		z-index: 4;

		grid-template-rows: repeat(2, auto);
		grid-auto-flow: row;
		justify-content: center;
		justify-items: center;
		align-content: center;
		gap: 30px;

		transition: opacity 0.2s ease-in, top 0.2s linear;
	}
}

.mobile-menu__nav {
	padding-bottom: 44px;
	display: grid;
	grid-auto-flow: row;
	gap: 30px;
	list-style-type: none;
}

.mobile-menu__link {
	font-weight: 600;
	font-size: 14px;
	line-height: 28px;
	text-transform: uppercase;
	text-decoration: none;
	color: $black;

	transition: 0.3s linear;
	outline: none;

	&:hover {
		color: $orange;
	}

	&:focus {
		color: $orange;
		text-shadow: 1px 1px 3px $orange;
	}
}

.mobile-menu__contacts {
	padding-top: 47px;
	border-top: 1px solid #e8e8e8;

	display: grid;
	grid-auto-flow: row;
	gap: 17px;
}

.mobile-menu__buttons-wrap {
	display: grid;
	grid-auto-flow: row;
	gap: 21px;
}

.mobile-menu__social {
	display: grid;
	justify-content: center;
	grid-template-columns: 60px 60px;
	gap: 11px;
	order: 2;
}

.mobile-menu .mobile-menu--active {
	display: block;
}

.mobile-menu--active {
	@media (max-width: 930px) {
		z-index: 98;
		display: grid;
		top: 0;
		opacity: 1;
		transition: top 0.2s linear, opacity 0.2s ease-in;
	}
}
