/* Section preferences */

.section-preferences {
	padding-top: 97px;
	padding-bottom: 100px;

	position: relative;

	&::after {
		content: "";
		display: block;
		width: 838px;
		height: 1265px;

		position: absolute;
		z-index: -1;
		top: -50px;
		left: 0;

		background-image: url("./../img/bg/bg-preferences.png");
		background-repeat: no-repeat;
		background-position: left;
		opacity: 0.6;

		@media (max-width: 930px) {
			background-image: none;
		}
	}

	@media (max-width: 1199px) {
		padding-top: 70px;
		padding-bottom: 70px;
	}
}

.section-preferences__content {
	position: relative;
}

.section-preferences__title {
	position: absolute;

	@media (max-width: 1199px) {
		width: 410px;
		line-height: 1.5;
	}

	@media (max-width: 930px) {
		position: relative;
		margin-bottom: 50px;
	}
}

.section-preferences__row {
	display: grid;
	grid-template-columns: repeat(2, auto);
	grid-template-rows: repeat(3, auto);
	grid-auto-flow: column;
	gap: 22px 31px;
	grid-template-areas:
		". preferences-block3"
		"preferences-block1 preferences-block4"
		"preferences-block2 preferences-block5";

	@media (max-width: 1199px) {
		gap: 30px;
	}

	@media (max-width: 930px) {
		display: block;
		margin-right: 30px;
		width: auto;
	}
}

/* Preferences block */

.preferences-block {
	display: grid;
	grid-template-columns: repeat(2, auto);
	align-items: flex-start;
	gap: 18px;

	@media (max-width: 930px) {
		width: 240px;
		margin-right: 22px;
		margin-left: 22px;
		outline: 0;
		transition: 0.3s linear;

		&:focus {
			text-shadow: 1px 1px 2px rgba(172, 100, 0, 0.3);
		}
	}
}

.preferences-block__icon {
	width: 50px;
	height: 50px;
	background: rgba(246, 155, 27, 0.15);

	display: flex;
	justify-content: center;
	align-items: center;

	svg {
		fill: $orange;
		width: 24px;
		height: 24px;
	}

	@media (max-width: 930px) {
		margin-bottom: 20px;
	}
}

.preferences-block__info {
	display: grid;
	gap: 11px;

	font-size: 16px;
	line-height: 1.75;
	color: $gray;
}

.preferences-block__title {
	margin-bottom: 10px;

	font-weight: 800;
	font-size: 16px;
	line-height: 1.88;
	text-transform: uppercase;
	color: $black;
}

.preferences-block1 {
	grid-area: preferences-block1;
}

.preferences-block2 {
	grid-area: preferences-block2;
}

.preferences-block3 {
	grid-area: preferences-block3;
}

.preferences-block4 {
	grid-area: preferences-block4;
}

.preferences-block5 {
	grid-area: preferences-block5;
}
